* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.App{
  text-align: center;
}
body {
  background-color: rgba(255, 255, 255, 0.8);
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  color: white;
}
.mircophone-container {
  display: flex;
  justify-content: center;
  width: 100vw;
  height: 80vh;
}
.microphone-icon-container {
  width: 140px;
  height: 140px;
  border-radius: 50%;
  background-image: linear-gradient(128deg, rgb(255, 0, 0), #ff0000);
  position: relative;
  cursor: pointer;
}
.microphone-icon-container.listening::before {
  content: "";
  width: 140px;
  height: 140px;
  background-color: #ff000081;
  position: absolute;
  top: 50%;
  left: 50%;
  transform:translate(-50%, -50%) scale(1.4);
  border-radius: 50%;
  animation: listening infinite 1.5s;
}
@keyframes listening{
  0%{
    opacity: 1;
    transform: translate(-50%, -50%) scale(1);
  }
  100%{
    opacity: 0;
    transform: translate(-50%, -50%) scale(1.4);
  }
}
.microphone-icon {
  width: 100%;
  height: 100%;
}
.microphone-status {
  font-size: 22px;
  min-width: 215px;
}
.btn {
  border: none;
  padding: 10px 30px;
  outline: none;
  cursor: pointer;
  font-size: 20px;
  border-radius: 25px;
  box-shadow: 0px 0px 10px 5px #ffffff1a;
}
.microphone-result-container {
  text-align: center;
  height: 50vh;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;

}

.microphone-reset {
  width: 150px;
  height: 150px;
  border: 1px solid #fff;
  background: black;
  color: rgb(255, 255, 255);
  margin-bottom: 500px;
 
}